import { configureStore, Middleware } from '@reduxjs/toolkit';
import favoritesReducer from '../features/favorites/favoritesSlice';
import cartReducer from '../features/cart/cartSlice';
import { productsApi } from './services/productsApi';

export const favoritesMiddleware: Middleware = store => next => action => {
  // Перевірка, що action є об’єктом з type
  if (action && typeof action.type === 'string' && action.type.startsWith('favorites/')) {
    const state = store.getState();
    localStorage.setItem('favorites', JSON.stringify(state.favorites.items));
  }

  return next(action);
};

const preloadedState = {
  favorites: {
    items:
      typeof window !== 'undefined' ? JSON.parse(localStorage.getItem('favorites') || '[]') : [],
  },
};

export const store = configureStore({
  reducer: {
    favorites: favoritesReducer,
    cart: cartReducer,
    [productsApi.reducerPath]: productsApi.reducer,
  },
  preloadedState,
  middleware: getDefaultMiddleware =>
    getDefaultMiddleware({
      serializableCheck: false, // важливо для localStorage
      thunk: true, // забезпечує підтримку async-функцій
    })
      .concat(favoritesMiddleware)
      .concat(productsApi.middleware),
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;